<template>
  <!-- 풋터 -->
  <div class="footer">
    <v-row class="footer_con">
      <v-col cols="9">
        <div class="logo_con">
          <router-link to="/">
            <img
              src="../../assets/logo/whitelogo.svg"
              alt=""
              class="footer_logo"
            />
          </router-link>
        </div>
        <div class="footer_info">
          <p>부산광역시 사상구 백양대로684 5F</p>
          <p>
            TEL 051-301-5908 | FAX 3030-3448-0523 | E-mail:
            gameinscokr@naver.com
          </p>
          <p>
            (주)게임인스 | 대표이사: 김웅겸 | 사업자등록번호: 362-88-00435 |
            통신판매업 등록번호: 1649-3196-3663-0809
          </p>

          <!-- 모바일 푸터 아이콘 -->
          <div class="small_btn_con" style="display: none">
            <hr style="width: 90vw; margin: 12px 0px" />
            <div
              class="small_btn"
              style="
                justify-content: center;
                align-items: center;
                display: flex;
                width: 90vw;
              "
            >
              <v-row>
                <v-col cols="3" style="justify-content: center; display: flex"
                  ><router-link to="/contact">{{
                    btn[2].text
                  }}</router-link></v-col
                >
                <v-col cols="3" style="justify-content: center; display: flex"
                  ><router-link to="/news">{{
                    btn[3].text
                  }}</router-link></v-col
                >
                <v-col
                  cols="3"
                  style="justify-content: center; display: flex"
                  >{{ btn[4].text }}</v-col
                >
                <v-col
                  cols="3"
                  style="z-index: 1; justify-content: center; display: flex"
                  ><router-link to="/way">{{ btn[5].text }}</router-link></v-col
                >
              </v-row>
            </div>
            <div style="display: flex; padding: 12px 0px">
              <a href=""
                ><img :src="btn[0].logo_image" style="margin-right: 16px"
              /></a>
              <a href="">
                <img :src="btn[1].logo_image" style="margin-right: 16px" />
              </a>
              <a href=""
                ><img :src="btn[2].logo_image" style="margin-right: 16px" />
              </a>
            </div>
          </div>
          <p class="footer_info_reserve">
            copyright 2022 GAMEINS All right reserved
          </p>
        </div>
      </v-col>
      <v-spacer />

      <v-col cols="3" class="contact_btn_con">
        <div class="contact_btn">
          <div v-for="(btnText, index) in btn" :key="index">
            <div class="btn">
              <a
                v-if="btnText.text === 'INSTAGRAM' || btnText.text === 'BLOG'"
                :href="btnText.link"
              >
                {{ btnText.text }}
                <img :src="btnText.logo_image" />
              </a>
              <router-link v-else :to="btnText.link">
                {{ btnText.text }}
                <img :src="btnText.logo_image" />
              </router-link>
            </div>
          </div>
        </div>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import store from "@/store";
//   import { onMounted, reactive } from "vue";
import { mapGetters } from "vuex";

export default {
  name: "Footer",
  data() {
    return {
      btn: [
        {
          link: "https://blog.naver.com/gameinscokr",
          text: "BLOG",
          logo_image: require("@/assets/footer/blog.svg"),
        },
        {
          link: "https://www.instagram.com/tikintaka_official",
          text: "INSTAGRAM",
          logo_image: require("@/assets/footer/insta.svg"),
        },
        {
          link: "/contact",
          text: "문의&의뢰",
          logo_image: require("@/assets/footer/message.svg"),
        },
        {
          link: "/news",
          text: "회사소식",
          logo_image: require("@/assets/footer/certification.svg"),
        },
        {
          link: "/",
          text: "개인정보처리방침",
          logo_image: require("@/assets/footer/personal.svg"),
        },
        {
          link: "/way",
          text: "오시는길",
          logo_image: require("@/assets/footer/locate.svg"),
        },
      ],
    };
  },
  computed: {
    ...mapGetters(["isScrolling"]),
  },
  mounted() {
    const footer_con = document.querySelector(".footer");

    footer_con.addEventListener("wheel", function (e) {
      console.log("footer-wheel");
      e.preventDefault();
      if (store.getters.isScrolling) {
        return;
      }
      store.dispatch("setIsScrolling", true);

      if (e.deltaY < 0) {
        window.scrollTo({
          top: window.scrollY - footer_con.scrollHeight,
          behavior: "smooth",
        });
      }

      setTimeout(() => {
        store.dispatch("setIsScrolling", false);
      }, 500);
    });

    const mobile_reactive = document.querySelector(".small_btn_con");
    const expand_reactive = document.querySelector(".contact_btn_con");

    if (window.innerWidth < 760) {
      mobile_reactive.style.display = "block";
      expand_reactive.style.display = "none";
    } else {
      mobile_reactive.style.display = "none";
      expand_reactive.style.display = "block";
    }

    window.addEventListener("resize", () => {
      if (window.innerWidth < 760) {
        mobile_reactive.style.display = "block";
        expand_reactive.style.display = "none";
      } else {
        mobile_reactive.style.display = "none";
        expand_reactive.style.display = "block";
      }
    });
  },
};
</script>

<style lang="scss" scoped>
@media screen and (min-width: 1280px) {
  .footer_con {
    display: flex;
    width: 1280px;
  }
  .btn {
    width: 180px;
    height: 36px;
    font-size: 0.9rem;
    a {
      width: 100%;
    }
  }
  .footer_info {
    font-size: 0.9rem;
  }
  .logo_con {
    margin-bottom: 8%;
  }
  .footer_logo {
    width: 150px;
    height: 36px;
  }
  .footer_info_reserve {
    padding-top: 26px;
  }
}
// 노트북
@media screen and (min-width: 760px) and (max-width: 1279px) {
  .footer_con {
    display: flex;
    width: 800px;
  }
  .btn {
    width: 160px;
    height: 30px;
    font-size: 0.725rem;
    a {
      width: 100%;
    }
  }
  .footer_info {
    font-size: 0.725rem;
  }
  sssssssssssssssssssssssssssss .logo_con {
    margin-bottom: 8%;
  }
  .footer_logo {
    width: 130px;
    height: 30px;
  }
  .footer_info_reserve {
    padding-top: 26px;
  }
}
// 태블릿
@media screen and (min-width: 600px) and (max-width: 759px) {
  .footer_con {
    width: 100%;
  }
  .btn {
    width: 100px;
    height: 20px;
    font-size: 12px;
    a {
      width: 0;
    }
  }
  .footer_info {
    font-size: 12px;
  }
  .footer_logo {
    width: 80px;
    height: 18px;
  }
  .logo_con {
    margin-bottom: 12px;
  }
}

// 휴대폰
@media screen and (max-width: 599px) {
  .footer_con {
    width: 100%;
  }
  .btn {
    width: 100px;
    height: 20px;
    font-size: 12px;
    a {
      width: 0;
    }
  }
  .footer_info {
    font-size: 12px;
  }
  .footer_logo {
    width: 80px;
    height: 18px;
  }
  .logo_con {
    margin-bottom: 12px;
  }
}

/* 풋터 */

.footer {
  position: relative;
  width: 100vw;
  height: auto;
  background-color: #2d2d2d;
  box-shadow: 0px 0px 1px 0px grey;
}

.footer_con {
  margin: auto;
}

.logo_con {
  padding-top: 12px;
}
.footer_logo {
  cursor: pointer;
}
.footer_info {
  color: #ffffff;
  font-family: "Pretendard-Regular";
  margin-top: 12px;
}
.contact_btn {
  display: grid;
  width: 100%;
  padding-top: 12px;
  justify-content: right;
}
.btn {
  border: solid 1px white;
  display: flex;
  align-items: center;
  padding: 12px;
  margin: 3px;
}
a {
  text-decoration: none;
  color: white;
  justify-content: space-between;
  display: flex;
}
</style>
